import { useDispatch, useSelector } from 'react-redux';
import { Link, useHistory } from 'react-router-dom';
import {
  Navbar,
  Nav,
  NavItem,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Container,
  Button,
  ButtonGroup,
} from 'reactstrap';
import { toast } from 'react-hot-toast';
import { getUserSuccess, resetApp, SwitchGroupStart } from '../../../store/actions';
import { ReactComponent as Logo } from '../../../assets/images/logo.svg';

const Header = () => {
  const dispatch = useDispatch();
  const logout = () => {
    dispatch(resetApp());
  };
  const history = useHistory();
  const { featureIds, uuid, groupName, loggedInGroup, username } = useSelector(
    state => state.auth.Currentuser,
  );
  const FeatureIds = featureIds?.split('~');
  const handleSuccess = (type, data) => {
    console.log({ type, data });
    if (type === 'success') {
      toast.success('Switched Group');
      dispatch(getUserSuccess(data));
      history.push('/dashboard');
      localStorage.removeItem('originalgrp');
      window.location.reload();
    } else {
      toast.error('User name or Email Already Exist!');
    }
  };
  console.log({ featureIds, uuid });
  const SwitchBack = () => {
    const valuesUser = {
      targetGroup: localStorage.getItem('originalgrp'),
      uuid,
      loggedInGroup: groupName || loggedInGroup,
      switchback: true,
    };
    dispatch(
      SwitchGroupStart({
        data: valuesUser,
        handleSuccess,
      }),
    );
  };
  return (
    <>
      <Navbar color="faded" light expand="md">
        <Container className="d-flex">
          <Link to="/dashboard" className="mt-1">
            <Logo />
          </Link>
          <Nav className="ml-auto" navbar>
            <UncontrolledDropdown nav inNavbar className="logout-dropdown">
              <DropdownToggle nav caret>
                <div className="dropdown-toggle-text">
                  <span className="f-14">Welcome,</span>
                  <span className="f-14 text-bold">
                    {username} ({groupName || loggedInGroup})
                  </span>
                </div>
              </DropdownToggle>
              <DropdownMenu right>
                <DropdownItem onClick={logout}>
                  <i className="fas fa-sign-out text-red mx-2" />
                  Logout
                </DropdownItem>
                {FeatureIds?.includes('FT_ID_20') && (
                  <DropdownItem onClick={SwitchBack}>
                    <i className="fas fa-sign-out text-red mx-2" />
                    Switch back
                  </DropdownItem>
                )}
              </DropdownMenu>
            </UncontrolledDropdown>
            <NavItem className="align-self-center">
              {FeatureIds?.includes('FT_ID_8') && (
                <Link to="/upload-list" className="no_td">
                  <ButtonGroup>
                    <Button outline color="danger">
                      <i className="far fa-folder" />
                    </Button>
                    <Button outline color="danger" className="text-semi-bold">
                      Shipment Files
                    </Button>
                  </ButtonGroup>
                </Link>
              )}
              {FeatureIds?.includes('FT_ID_9') && (
                <Link to="/access-management" className="no_td mx-3">
                  <ButtonGroup>
                    <Button outline color="danger" className="text-semi-bold">
                      Access Management
                    </Button>
                  </ButtonGroup>
                </Link>
              )}
            </NavItem>
          </Nav>
        </Container>
      </Navbar>
    </>
  );
};

export default Header;
